.App {
    text-align: center;
    flex-direction: row;
    background-position: center;
    background-repeat: no-repeat;
    /*width: 1908px;*/
    /*height: 100vh;*/
  }
  
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 95.5vh;
    display: flex;
    flex-direction: column;
    /*align-items: center;*/
    /*justify-content: center;*/
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

  .Tab {
    height: 5vh;
  }

  .padding-0{
    padding-right:0;
    padding-left:0;
  }

  .View {
    height: 100vh;
    width: 100vw;
  }

  .ThirdPartyView {
    background-color: #282c34;
    min-height: 95.5vh;
    
    flex-direction: column;
    /*align-items: center;*/
    /*justify-content: center;*/
    font-size: calc(10px + 2vmin);
    color: white;
  }

  .Login {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #282c34;
  }

  .LoginForm {
    width: 60vmax;
    min-width: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: aliceblue
  }

  .LoanApply {
    text-align: center;
    flex-direction: row;
    background-size: 100vmax;
    background-position: center;
    background-repeat: no-repeat;
    /*width: 100vw;
    height: 100vh;*/
    color: #666666;
  }

  .LoginButton {
    margin: 1vmin;
    min-width: 100px;
    max-width: 20vmax;
  }

  .Page {
    height: 100vh;
    width: 100vw;
  }

  .PageTitle {
    font-size: calc(12px + 4vmin);
    color: #000;
  }

  .ListView {
    overflow: 'auto';
    min-height: 200px; 
    width: 40vw;
    margin-top: 6vh;
    margin-bottom: 6vh;
    margin-left: 4vw;
    margin-right: 4vw;
  }

  .ListTile {
    text-align: left;
    background-color: #666666;
    min-height: 180px;
    height: 20vh;
    font-size: 10 + 2vmin
  }